
html {
  height: 100%;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#0F87F5), to(#3D52FF));
  background-image: -webkit-linear-gradient(#0F87F5, #3D52FF);
  background-image: -o-linear-gradient(#0F87F5, #3D52FF);
  background-image: linear-gradient(#0F87F5, #3D52FF);
}

header {
  position: fixed;
  top: 0;
  width: 100%;
}

h1 {
  text-align: center;
  color: white;
}

#content {
  margin: 65px auto 0 auto;
  padding: 15px;
  width: 85%;
  max-width: 960px;
  min-width: 350px;
}


